.container {
  text-align: center;
  padding: 20px;
}

select {
  padding: 8px;
  margin: 15px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 15px;
}

.card {
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  transition: 0.3s;
}

.card:hover {
  transform: scale(1.05);
}
